.login {
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login__wrapper {
    width: 70%;
    height: 70%;
    display: flex;
}

.login__left,
.login__right {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.login__logo {
    font-size: 50px;
    font-weight: 800;
    color: #1775ee;
    margin-bottom: 10px;
}

.login__description {
    font-size: 24px;
}

.login__box {
    height: 330px;
    padding: 20px;
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.login__input {
    height: 50px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
    margin-bottom: 10px;
    outline: none;
}

.login__input:focus {
    outline: none;
}

.login__button {
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #1775ee;
    color: #fff;
    cursor: pointer;
    outline: none;
    font-size: 20px;
    font-weight: 500;
}

.login__button:disabled {
    cursor: not-allowed;
}

.login__forgotPassword {
    text-align: center;
    color: #1775ee;
}

.login__register {
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #42b728;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width:885px){
    .login__wrapper{
        flex-direction: column;
    }
    .login__box{
        margin-top: 15px;
    }
}